#calculator {
  display: flex;
  flex-direction: column;
  width: 200px;
  margin: 0 auto;
}

#display {
  background-color: #333;
  color: white;
  font-size: 2em;
  padding: 10px;
  text-align: right;
}

#buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

button {
  font-size: 1.5em;
  padding: 20px;
  border: 1px solid #ccc;
  cursor: pointer;
}

button:active {
  background-color: #ddd;
}

button#equals {
  grid-column: span 4;
  background-color: #ff5722;
  color: white;
}
